.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: 'Libre Baskerville', serif;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 1rem;
}

.h1 {
  font-size: 2rem;
}

.h2 {
  font-size: 1.8rem;
}

.h3 {
  font-size: 1.6rem;
}

.h4 {
  font-size: 1.4rem;
}

.h5 {
  font-size: 1.2rem;
}

.h6 {
  font-size: 1rem;
}
